<template>
    <div class="scaling-plan-h5">
        <publicMWrap :title="t('Challenges.ScalingPlan.title')">
            <publicTabBar :tabList="tabList" @trigger="trigger"></publicTabBar>
            <div class="scaling-plan-h5__collapse">
                <div class="list">
                    <div class="list-item" v-for="val in 4" :key="val">
                        <span class="item-label">挑战账户的余额</span>
                        <span class="item-val">$10,000</span>
                    </div>
                </div>
                <button class="btn" @click="linkTo">{{t('Challenges.ScalingPlan.buy')}} $1699</button>
            </div>
        </publicMWrap>
    </div>
</template>
<script setup>
import { TAB_LIST } from "@/data";
import {useChallenges} from "@/store/challenges"
const tabList = reactive(TAB_LIST);
const { t } = useI18n();
let battleId = ref(0)
// 獲取數據
const getList = () => {
    const res = useChallenges().scalingPlan
    res.map((item) => {
		item.levelTag = `Home.challenge.level${item.level}.name`
	})
}
const trigger = (tab) => {
}
onMounted(()=> {
    getList()
})
const linkTo = ()=>{
	nuxtTo("/user",{
        idx: battleId.value
    })
}
</script>
<style scoped lang='scss' src="./index.scss"></style>